#contact{
    margin: 3% auto;
    max-width: 50vw;
    background-color: #F5F5F5;
    padding: 5%;
}

#contact input,#contact textarea{   
    width: 100%;
    padding: 2%;
    margin-bottom: 1%;
}

@media only screen and (max-width: 768px) {
    #contact {
        max-width: 100vw;
    }
}